@import "toolkit/vars"; //Custom vars that override bootstrap vars
@import "toolkit/foundations/typography";
@import "toolkit/foundations/tk-icons"; //custom toolkit icons, file generated by gulp
@import "toolkit/foundations/tk-icons-helper"; //css for better gulp icons, wont be need when icons become a plugin

/* CSS used for test map */

body {
	margin: 0;
	padding: 0;
}

.gm-wrapper {
    width: 100%;
    padding-bottom: 50%;
    position: relative;
    border: 1px solid red;
}

/**
 * Tabs
 */

.gm-tabs {

	position: absolute;
	bottom: 0;
	left: 0;
	z-index: 2;	
	padding: 0;
	margin: 0;

	li {
		list-style: none;
		display: inline-block;
	}

	li a {
		background: $color-masthead;
		display: block;
		padding: 10px;
		margin: 0;
		color: white;
		@extend %heading-related;

		&:hover,
		&:focus {
			text-decoration: none;
			background: $color-masthead-dark;
		}

		&.active {
			background: $color-brand-1;
			&:hover,
			&:focus {
				text-decoration: none;
				background: $color-brand-1-dark;
			}
		}
	}

}

/**
 * Flyouts
 */

.gm-flyout {
	position: absolute;
	width: 33.33%;
	height: 100%;
	background: rgba(1,1,1,.88);
	top: 0;
	left: 0;
}

.gm-flyout-content {
	color: white;
	padding: 50px $space;
}

.gm-flyout-close {
	width: 20px;
	height: 20px;
	top: 5px;
	right: 5px;
	position: absolute;
	background: white;
	text-indent: -9999px;
}

.gm-info-window {

	.gm-title {
		display: block;
		font-size: 14px;		
	}

	.gm-content {
		line-height: 170%;
		a {
			border-bottom: 1px solid $color-border;
		}
	}
}

#map,
#accmap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    margin-bottom: $space;
}

