/* CSS used for test map */
body {
  margin: 0;
  padding: 0; }

.gm-wrapper {
  width: 100%;
  padding-bottom: 50%;
  position: relative;
  border: 1px solid red; }

/**
 * Tabs
 */
.gm-tabs {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  padding: 0;
  margin: 0; }
  .gm-tabs li {
    list-style: none;
    display: inline-block; }
  .gm-tabs li a {
    background: #212121;
    display: block;
    padding: 10px;
    margin: 0;
    color: white; }
    .gm-tabs li a:hover, .gm-tabs li a:focus {
      text-decoration: none;
      background: #111111; }
    .gm-tabs li a.active {
      background: #6b3f88; }
      .gm-tabs li a.active:hover, .gm-tabs li a.active:focus {
        text-decoration: none;
        background: #492a5d; }

/**
 * Flyouts
 */
.gm-flyout {
  position: absolute;
  width: 33.33%;
  height: 100%;
  background: rgba(1, 1, 1, 0.88);
  top: 0;
  left: 0; }

.gm-flyout-content {
  color: white;
  padding: 50px 16px; }

.gm-flyout-close {
  width: 20px;
  height: 20px;
  top: 5px;
  right: 5px;
  position: absolute;
  background: white;
  text-indent: -9999px; }

.gm-info-window .gm-title {
  display: block;
  font-size: 14px; }

.gm-info-window .gm-content {
  line-height: 170%; }
  .gm-info-window .gm-content a {
    border-bottom: 1px solid #c9bfb5; }

#map,
#accmap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  margin-bottom: 16px; }

